*/
[screensize="s"][landscape] flexus-toolbar {
	--gutter: 8px;
	--toolbar-font-size: 14px;
}


flexus-toolbar {

	//.fx-toolbar;

	//&[acrylic],
	&[translucent],
	&[transparent] {
		box-shadow: none;
	}

	font-weight: 500 !important;

	button {
		// cancel default button hoverable/clickable behavior
		padding: 0 !important;
		min-width: 0 !important;
		height: initial !important;
		min-height: 24px;
	}

	//a[icon]:not([fab]),
	//button[icon]:not([fab]) {
	[icon] {
		// cancel default button hoverable/clickable behavior
		//padding: 0 !important;
		//min-width: 0;
		display: inline-flex;
		/*&:empty::before {
			margin-right: 0 !important;
		}
		&:not(:empty)::before {
			margin-right: 0px !important;
		}*/
		/*&::before {
			width: auto !important;
		}*/

		position: relative;
		&:empty {
			width: 24px;
			height: 24px;
			line-height: 40px;
		}
		//background-color: rgba(255,0,0,0.4);

		&[icon="more"] {
			width: 12px;
		}

	}

	// Hitbox
	// button[icon]:empty size is only 24px but touch hitbox should be at least 48px
	// thus overwrite default hitbox values. But keep in mind the 16px space between icons
	// doesn't allow for hitbox wider than 40px
	[touch] & button::after {
	//[touch] & button[icon]:empty::after {
		margin: -12px -8px;
	}

	input[type="search"],
	input[transparent]:not([type="checkbox"]),
	section[search] input:not([type="checkbox"]) {
		flex: 1;
		margin: 0;
		padding: 0;
		border: none;
		height: 100%;
		margin-left: -16px;
		padding-left: 16px;
		font-size: inherit;
		font-weight: inherit;
		&::-webkit-input-placeholder {
			color: inherit;
			opacity: 0.5;
		}
	}

	/*&:not([multisection]),
	&[multisection] > section {
		.fx-item-mixin-material;
	}*/

	&[multisection] > section {

		&[free] {
			// todo - try to unify naming with metro [fx-item]
			padding-top: 16px;
			padding-bottom: 16px;
			&[narrow] {
				padding-top: 8px;
				padding-bottom: 8px;
			}
			&[spacious] {
				padding-top: 24px;
				padding-bottom: 24px;
			}
		}

	}

	&:not([multisection]),
	&[multisection] > section:not([flexible]):not([free]) {
		min-height: var(--toolbar-min-height);
		font-size: var(--toolbar-font-size);
	}

	&[card],
	& > [card] {
		--fx-item-padding-horizontal: calc(1rem - 8px);
	}
	&[card] {
		.fx-elevation-4dp;
		position: absolute;
		left: 8px;
		right: 8px;
		top: 8px;
		height: 48px;
		padding: 8px 16px;
		--indent: 48px;
	}
	& > [card] {
		min-height: 48px !important;
		padding-top: 0;
		padding-bottom: 0;
		margin: 8px;
	}

	// TODO zkusit to vyeit nasypnm [main] [search] a [selection] sekci do jednoho
	// shadow seskupen a tomu nastavit 64px
	&[hascard] > section:not([flexible]):not([free]) {
		min-height: 64px;
	}


	// multisection fading
	[showing] & [icon]:empty::before,
	[showing] [icon]:empty::before {
		animation: icon-rotate-show 200ms ease-in-out forwards;
	}
	[hiding] & [icon]:empty::before,
	[hiding] [icon]:empty::before {
		animation: icon-rotate-hide 200ms ease-in-out forwards;
	}

}

@keyframes icon-rotate-show {
	from {transform: scale(0) rotate(-90deg)}
	to   {transform: scale(1) rotate(0deg)}
}
@keyframes icon-rotate-hide {
	from {transform: scale(1) rotate(0deg)}
	to   {transform: scale(0) rotate(90deg)}
}







flexus-tabs {
	font-size: 14px;
	line-height: 14px;
	text-transform: uppercase;
	font-weight: 500;

		& > * {
			height: 48px !important;
			text-transform: uppercase;
		}
		&[spacious] > * {
			padding: 0 1rem;
		}
		& > *[icon]:not(:empty) {
			height: 72px;
			justify-content: flex-end;
			padding-bottom: 16px;
			&:before {
				margin-bottom: 6px;
			}
		}
}



